'use client';

import { useState } from 'react';
import { Bell, ChevronDown, Search } from 'lucide-react';

export default function TopNav() {
    const [profileOpen, setProfileOpen] = useState(false);
    const [notificationsOn, setNotificationsOn] = useState(true);

    return (
        <header className="sticky top-0 z-10 border-b border-slate-200 bg-white/90 backdrop-blur">
            <div className="flex flex-wrap items-center justify-between gap-4 px-4 py-4 sm:px-6">
                <div className="flex flex-1 items-center gap-3">
                    <div className="relative w-full max-w-md">
                        <Search className="pointer-events-none absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-slate-400" />
                        <input
                            type="search"
                            placeholder="Search transactions, customers, payouts..."
                            className="w-full rounded-xl border border-slate-200 bg-slate-50 py-2.5 pl-10 pr-3 text-sm text-slate-700 outline-none transition focus:border-slate-300 focus:bg-white"
                        />
                    </div>
                </div>

                <div className="flex items-center gap-4">
                    <button
                        type="button"
                        onClick={() => setNotificationsOn((prev) => !prev)}
                        className={`flex items-center gap-2 rounded-full border px-3 py-1.5 text-xs font-semibold transition ${
                            notificationsOn
                                ? 'border-emerald-200 bg-emerald-50 text-emerald-700'
                                : 'border-slate-200 bg-slate-50 text-slate-500'
                        }`}
                        aria-pressed={notificationsOn}
                    >
                        <Bell className="h-4 w-4" />
                        {notificationsOn ? 'Notifications on' : 'Notifications off'}
                    </button>

                    <div className="relative">
                        <button
                            type="button"
                            onClick={() => setProfileOpen((prev) => !prev)}
                            className="flex items-center gap-3 rounded-full border border-slate-200 bg-white px-2 py-1.5 text-sm font-medium text-slate-700 shadow-sm transition hover:border-slate-300"
                            aria-expanded={profileOpen}
                            aria-haspopup="menu"
                        >
                            <img
                                src="https://i.pravatar.cc/80?img=15"
                                alt="Profile"
                                className="h-8 w-8 rounded-full object-cover"
                            />
                            <span className="hidden text-sm sm:inline">Amara Lewis</span>
                            <ChevronDown className="h-4 w-4 text-slate-400" />
                        </button>

                        {profileOpen ? (
                            <div className="absolute right-0 mt-3 w-48 overflow-hidden rounded-xl border border-slate-200 bg-white shadow-lg">
                                <a
                                    href="#"
                                    className="block px-4 py-3 text-sm text-slate-600 transition hover:bg-slate-50"
                                >
                                    Profile
                                </a>
                                <a
                                    href="#"
                                    className="block px-4 py-3 text-sm text-slate-600 transition hover:bg-slate-50"
                                >
                                    Settings
                                </a>
                                <a
                                    href="#"
                                    className="block px-4 py-3 text-sm text-rose-500 transition hover:bg-rose-50"
                                >
                                    Logout
                                </a>
                            </div>
                        ) : null}
                    </div>
                </div>
            </div>
        </header>
    );
}